<template>
  <div class="alet_container">
    <section class="tip_text_container">
      <div class="tip_icon">
        <span></span>
        <span></span>
      </div>
      <p class="tip_text">{{alertText}}</p>
      <div class="confrim" @click="closeTip">确认</div>
    </section>
  </div>
</template>

<script>
  export default {
    data () {
      return {
        positionY: 0,
        timer: null
      }
    },
    mounted () {

    },
    props: ['alertText'],
    methods: {
      closeTip () {
        this.$emit('closeTip')
      }
    }
  }
</script>

<style lang="scss" scoped>
  @import '../../style/mixin.scss';
  @keyframes tipMove{
    0%   { transform: scale(1) }
    35%  { transform: scale(.8) }
    70%  { transform: scale(1.1) }
    100% { transform: scale(1) }
  }
  .alet_container{
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 200;
    display: flex;
    justify-content: center;
  }
  .tip_text_container{
    width: 100%;
    margin: auto 10rem;
    left: 2rem;
    animation: tipMove .4s ;
    background-color: rgba(255,255,255,1);
    border: 1px;
    padding-top: .6rem;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    border: 1px solid #000000;
    border-radius: 0.25rem;
    /*background-color: #000000;*/
    /*opacity: 0.8;*/

  .tip_icon{
  @include wh(6rem, 6rem);
    border: 0.15rem solid #f85c64;
    margin-top: 4rem;
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
  span:nth-of-type(1){
  @include wh(.12rem, 1.5rem);
    background-color: #f85c64;
  }
  span:nth-of-type(2){
  @include wh(.2rem, .2rem);
    border: 1px;
    border-radius: 50%;
    margin-top: .2rem;
    background-color: #f85c64;
  }
  }
  .tip_text{
  @include sc(4rem, #333);
    line-height: 8rem;
    text-align: center;
    margin-top: 2rem;
    padding: 0 .4rem;
  }
  .confrim{
  @include sc(4rem, #fff);
    font-weight: bold;
    background-color: #1a9fff;
    width: 100%;
    text-align: center;
    line-height: 8rem;
    border: 1px;
    border-bottom-left-radius: 0.25rem;
    border-bottom-right-radius: 0.25rem;
  }
  }

</style>



